'use client';

import { FaTools, FaShippingFast, FaShieldAlt, FaHeadset, FaCertificate, FaUserTie } from 'react-icons/fa'

export default function WhyChooseUs() {
  const features = [
    {
      icon: <FaShieldAlt className="text-orange-400 text-2xl" />,
      title: 'Garantía Extendida',
      description: 'Incluye garantía de hasta 5 años en partes y mano de obra.',
    },
    {
      icon: <FaShippingFast className="text-orange-400 text-2xl" />,
      title: 'Envío e Instalación',
      description: 'Entrega e instalación profesional en todo el país.',
    },
    {
      icon: <FaTools className="text-orange-400 text-2xl" />,
      title: 'Mantenimiento',
      description: 'Servicio técnico especializado y mantenimiento programado.',
    },
    {
      icon: <FaHeadset className="text-orange-400 text-2xl" />,
      title: 'Soporte 24/7',
      description: 'Atención al cliente las 24 horas para cualquier emergencia.',
    },
    {
      icon: <FaCertificate className="text-orange-400 text-2xl" />,
      title: 'Calidad Certificada',
      description: 'Equipos certificados internacionalmente.',
    },
    {
      icon: <FaUserTie className="text-orange-400 text-2xl" />,
      title: 'Asesoría Experta',
      description: 'Especialistas te ayudan a elegir el equipo ideal.',
    },
  ];

  return (
    <section className="bg-dark text-white py-16 px-4">
      {/* Título de la sección */}
      <div className="text-center mb-12 max-w-3xl mx-auto">
        <h2 className="text-4xl font-bold mb-4">¿Por Qué Elegirnos?</h2>
        <p className="text-gray-300">
          Somos líderes en el mercado de equipamiento deportivo con más de 15 años de experiencia.
          Nuestro compromiso es brindar la mejor calidad y servicio.
        </p>
      </div>

      {/* Tarjetas */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6 max-w-7xl mx-auto">
        {features.map((feature, index) => (
          <div
            key={index}
            className="bg-[#1f1f1f] border border-gray-700 rounded-xl p-6 shadow hover:shadow-lg transition"
          >
            <div className="flex items-start gap-4">
              {/* Icono */}
              <div className="bg-[#2b2b2b] p-3 rounded-lg">
                {feature.icon}
              </div>

              {/* Texto */}
              <div>
                <h4 className="text-lg font-semibold">{feature.title}</h4>
                <p className="text-gray-400 text-sm">{feature.description}</p>
              </div>
            </div>
          </div>
        ))}
      </div>
    </section>
  );
}

